export const APP_TYPE = {
  DIR: 4,
  APP: 3
}

export function componentIndexOf(tasks, id) {
  for (let i = 0; i < tasks.length; i++) {
    if (tasks[i].el_id === id) {
      return i;
    }
  }
  return -1;
}


