/*,:,:2018-05-12 16:51:08*/
<template>
  <Explorer :id="userObject.route_url" title="" :resize="false" :min="false" :max="false"
            :handleClose="handleClose" :winHeight="300" :winWidth="300">
    <div slot="content" style="padding-left: 20px; padding-right: 20px; padding-top: 10px">
      <el-form :model="form" ref="form" :rules="rules" label-width="100px">
        <el-form-item label="" prop="title">
          <el-input placeholder="" size="small" v-model="form.title"></el-input>
        </el-form-item>
        <el-form-item label="" prop="tip">
          <el-input placeholder="" size="small" v-model="form.tip"></el-input>
        </el-form-item>
        <el-form-item label="" prop="status">
          <el-switch
            v-model="form.status"
            active-color="#13ce66"
            inactive-color="#ff4949"
            :active-value="1"
            :inactive-value="0"
          >
          </el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" style="text-align: right">
        <el-button @click="handleClose" size="small"></el-button>
        <el-button type="primary" @click="save()" size="small"></el-button>
      </div>
    </div>
  </Explorer>
</template>
<script>
  import Explorer from '../../commons/Explorer.vue';
  import http from '../../../plugins/http';
  import  {APP_TYPE} from '../../../constant';
  export default {
    props: ['userObject', 'options', 'index'],
    data() {
      return {
        title: '',
        form: this.initForm(),
        dialogMode: "save",
        show: false,
        rules: {
          title: [
            {required: true, message: '', trigger: 'blur'},
          ],
          tip: [
            {required: true, message: '', trigger: 'blur'},
          ],
          status: [
            {required: true, message: '', trigger: 'blur'},
          ],
        }
      }
    },
    methods: {
      save() {//
        const that = this;
        this.$refs['form'].validate((valid) => {
          if (!valid) {
            return;
          }
          const data = {
            ...that.form,
            type: APP_TYPE.DIR,
            parent_id: that.userObject.parent_id
          };
          that.$http.post("/api/appIcons/" + that.dialogMode, JSON.stringify(data)).then(res => {
            that.show = false;
            that.$message.success("!");
            that.refresh();
            that.handleClose();
          }).catch(res => {
            that.$message.error("!" + res);
          });
        });
      },
      initForm() {//
        return {
          type: null,
          title: null,
          tip: null,
          icon: null,
          publish_id: null,
          create_time: null,
          parent_id: null,
          route_url: null,
          status: 1,
          name: ''
        }
      },
      addDialog() {//
        this.title = "";
        this.dialogMode = "save";
        this.form = this.initForm();
        this.show = true;
      },
      editDialog(row) {//
        this.title = "";
        this.dialogMode = "update";
        this.form = {...row};
        this.show = true;
      },
      refresh(){
        //
        this.$store.commit("fisheye/refreshCurrent", this.userObject.parent_id);
      },
      handleClose: function (e) {
        const _this = this;
        this.$store.dispatch('desktop/removeComponent', {
          task: _this.userObject,
          index: _this.index
        })
        this.$store.commit('taskbar/removeTask', this.userObject)
      },
    },
    components: {Explorer}
  }
</script>
<style></style>



