<template>
  <Explorer :id="'controlPane_id'" title="" :resize="false" :min="false" :max="false" :handleClose="handleClose">
    <div slot="content">
      <el-tabs v-model="tabActive" type="card">
        <el-tab-pane label="" name="first"></el-tab-pane>
        <el-tab-pane label="" name="second"></el-tab-pane>
        <el-tab-pane label="" name="third"></el-tab-pane>
        <el-tab-pane label="" name="fourth"></el-tab-pane>
      </el-tabs>
    </div>
  </Explorer>
</template>
<script>

  import Explorer from '../commons/Explorer.vue';

  export default {
    props: ['userObject', 'options', 'index'],
    data: function () {
      return {
        tabActive: null,
      }
    },
    computed: {},
    created: function () {
    },
    mounted: function () {
    },
    methods: {
      handleClose: function (e) {
        const _this = this;
        this.$store.dispatch('desktop/removeComponent', {
          task: _this.userObject,
          index: _this.index
        })
      }
    },
    components: {
      Explorer
    }
  }
</script>
<style>

</style>



