<template>
  <Explorer :id="'ssh'" title="ipmi " :resize="false" :min="false" :max="false" :handleClose="handleClose">
    <div slot="content">
      <el-row :gutter="10" v-loading="loading" element-loading-text="" style="overflow-y: auto">
        <el-col :span="8" v-for="ipmi in ipmis">
          <el-card shadow="hover" style="text-align: center ;cursor: pointer">
            <img src="../../../assets/os/ipmi.jpg" width="100%"/>
            <el-button-group>
              <el-button size="mini" type="danger" icon="fa fa-power-off" title=""></el-button>
              <el-button size="mini" type="success" icon="fa fa-play-circle" title=""></el-button>
              <el-button size="mini" type="warning" icon="fa fa-spinner" title=""></el-button>
              <el-dropdown>
                <el-button size="mini" type="primary" icon="fa fa-ellipsis-h" title=""></el-button>
                <el-dropdown-menu slot="dropdown">
                  <el-dropdown-item></el-dropdown-item>
                  <el-dropdown-item></el-dropdown-item>
                  <el-dropdown-item></el-dropdown-item>
                </el-dropdown-menu>
              </el-dropdown>
            </el-button-group>
          </el-card>
        </el-col>
      </el-row>
    </div>
  </Explorer>
</template>
<script>
  import Explorer from '../../commons/Explorer.vue';
  import http from '../../../plugins/http';
  export default {
    props: ['userObject', 'options', 'index'],
    data: function () {
      return {
        loading: false,
        ipmis: []
      }
    },
    created: function () {
      this.loading = true;
      const _this = this;
      this.$http.post('/api/protocols/queryList', JSON.stringify({
        type: 4
      })).then(function (res) {
        _this.ipmis = [1, 2, 3, 4];
        _this.loading = false;
      }).catch(function (res) {
        _this.ipmis = [];
        _this.loading = false;
      })
    },
    methods: {
      handleConnect: function (index, row) {
        var app = {
          id: 'ssh01',
          title: 'ssh',
          route_url: '/guacamole.html?id=' + row.id + "&type=ssh",
          icon: require('../../../assets/icon/sidebar/putty.png')
        }
        this.$store.dispatch('taskbar/addBrowser', app);
      },
      handleClose: function (e) {
        const _this = this;
        this.$store.dispatch('desktop/removeComponent', {
          task: _this.userObject,
          index: _this.index
        })
        this.$store.commit('taskbar/removeTask', this.userObject)
      },
    },
    mounted: function () {

    },
    components: {
      Explorer
    }
  }
</script>
<style>

</style>



